@import url(https://fonts.googleapis.com/css?family=Lato);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* modified from https://codepen.io/madshaakansson/pen/xwmzK */

nav * {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: 'Lato', sans-serif;
}

nav {
  top: 0;
  left: 0;
  width: 100%;
  background: #fff;
  box-shadow: 0 3px 10px -2px rgba(0,0,0,.1);
  border: 1px solid rgba(0,0,0,.1);

  position: fixed;
  z-index: 999;
  /* padding: 15px; */

  color: #222;
  font-size: .9em;
  letter-spacing: 1px;
  text-decoration: none;
  text-transform: uppercase;
}

@media only screen and (max-width: 650px) {
  nav {
    height: 80px;
    min-height: 80px;
  }
}

@media only screen and (min-width: 651px) {
  nav {
    height: 40px;
    min-height: 40px;
  }
}

nav *::after {
  /* margin-bottom: 20px; */
}

nav > b {
  display: inline-block;
  padding: 10px 20px;
  float: left;
}

nav > ul {
list-style: none;
position: relative;
float: left;
margin-left: 40px;
margin-right: 16px;
}

nav > ul > li{
  float: left;
  transition: all .2s ease-in-out;
}

nav > ul li:hover{
  background: rgba(0,0,0,.15);
}

nav > ul li:hover > ul{
  display: block;
}

nav > ul li {
  float: left;
  transition: all .2s ease-in-out;
}

nav > ul ul li::marker {
  color: transparent;
}

nav ul li a {
  display: block;
  padding: 12px 20px;
  color: #222;
  font-size: .9em;
  letter-spacing: 1px;
  text-decoration: none;
  text-transform: uppercase;
}

nav > ul ul {
  display: none;
  background: #fff;
  position: absolute;
  z-index: 999;
  top: 100%;
  box-shadow: -3px 3px 10px -2px rgba(0,0,0,.1);
  border: 1px solid rgba(0,0,0,.1);
}

nav > ul ul li a {
  padding: 15px 30px;
  border-bottom: 1px solid rgba(0,0,0,.05);
}

.App {
  position: relative;
  background-color: #282c34;
  min-width: 100vw;
  min-height: 100vh;
  color-scheme: dark;
}

h3 {
  -webkit-margin-after: 0;
          margin-block-end: 0;
  margin-top: 1.5em;
}

small {
  font-size: x-small;
}

@media only screen and (max-width: 650px) {
  .App {
    padding-top: 80px;
  }
}

@media only screen and (min-width: 651px) {
  .App {
    padding-top: 40px;
  }
}

.infopage p, .infopage ul {
  font-size: 14px;
  line-height: 25px;
  padding-right: 50px;
}

.room-img {
  height: 500px;
  left: 0;
  top: 0;
  float: left;
}

.App-header {
  font-size: calc(6px + 2vmin);
  color: white;
  padding-left: 510px;
  padding-top: 10px;
  padding-right: 50px;
}

.dot {
  width: 10px;
  height: 10px;
  border-radius: 10px;
  background-color: red;
  position: absolute;
  transition: opacity 1000ms, -webkit-transform 1000ms;
  transition: opacity 1000ms, transform 1000ms;
  transition: opacity 1000ms, transform 1000ms, -webkit-transform 1000ms
}

.canvas {
  position: absolute;
  width: 500px;
  height: 500px;
}

