/* modified from https://codepen.io/madshaakansson/pen/xwmzK */

@import url(https://fonts.googleapis.com/css?family=Lato);

nav * {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: 'Lato', sans-serif;
}

nav {
  top: 0;
  left: 0;
  width: 100%;
  background: #fff;
  box-shadow: 0 3px 10px -2px rgba(0,0,0,.1);
  border: 1px solid rgba(0,0,0,.1);

  position: fixed;
  z-index: 999;
  /* padding: 15px; */

  color: #222;
  font-size: .9em;
  letter-spacing: 1px;
  text-decoration: none;
  text-transform: uppercase;
}

@media only screen and (max-width: 650px) {
  nav {
    height: 80px;
    min-height: 80px;
  }
}

@media only screen and (min-width: 651px) {
  nav {
    height: 40px;
    min-height: 40px;
  }
}

nav *::after {
  /* margin-bottom: 20px; */
}

nav > b {
  display: inline-block;
  padding: 10px 20px;
  float: left;
}

nav > ul {
list-style: none;
position: relative;
float: left;
margin-left: 40px;
margin-right: 16px;
}

nav > ul > li{
  float: left;
  -webkit-transition: all .2s ease-in-out;
  -moz-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}

nav > ul li:hover{
  background: rgba(0,0,0,.15);
}

nav > ul li:hover > ul{
  display: block;
}

nav > ul li {
  float: left;
  -webkit-transition: all .2s ease-in-out;
  -moz-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}

nav > ul ul li::marker {
  color: transparent;
}

nav ul li a {
  display: block;
  padding: 12px 20px;
  color: #222;
  font-size: .9em;
  letter-spacing: 1px;
  text-decoration: none;
  text-transform: uppercase;
}

nav > ul ul {
  display: none;
  background: #fff;
  position: absolute;
  z-index: 999;
  top: 100%;
  box-shadow: -3px 3px 10px -2px rgba(0,0,0,.1);
  border: 1px solid rgba(0,0,0,.1);
}

nav > ul ul li a {
  padding: 15px 30px;
  border-bottom: 1px solid rgba(0,0,0,.05);
}
